import React, {Component} from 'react';
import './style.css';


class Header extends Component {
    constructor(props){
        super(props)
        this.state = {
            headerBg: 'transparent',
            borderBottomHeader: 'transparent'
        };

        const whatsappBt = document.querySelector('#whatsapp_bt');

        this.handleScroll = (event) =>{
            if(window.scrollY > (window.innerHeight / 3)) {
                this.setState({headerBg: '#0080aa'});
                this.setState({borderBottomHeader: '1vh solid rgba(0,0,0,0.2)'})
                if(whatsappBt) {
                    whatsappBt.style.transform = "translateY(0)"
                }
            }else{
                this.setState({headerBg: 'transparent'})
            }
        }

        
    }
    
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll, { passive: true })
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll)
    }
    render() {
        return (
            <header id="main-header" style={{backgroundColor: this.state.headerBg}}>
                <nav>
                    <a href="#home">Início</a>
                    <a href="#produtos">Produtos</a>
                    <a href="#servicos">Serviços</a>
                    <a href="#contato">Contato</a>
                </nav>
        
                <div className="mobile_only" id="hamburguer">
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
            </header>
        );
    }
}


export default Header;