import React, {Component} from 'react';
import './style.css'
import Header from './components/Header';
import Subheader from './components/Subheader';
import Features from './components/Features';
import Produtos from './components/Produtos';
import Servicos from './components/Servicos';
import Contact from './components/Contact';
import bg4 from './assets/bg4.jpg';

import {BrowserRouter, Route} from 'react-router-dom';
import ControleFinanceiro from '../src/components/ControleFinanceiro'

class App extends Component {
  
  constructor(props){
    super(props)
    this.state = {
        opacity: '1'
    };

    document.onreadystatechange = () => {
      if (document.readyState === "complete") {

        let showingNav = false;
        const navBars = document.querySelectorAll('#main-header nav a');
        const firstHamBar = document.querySelector('#hamburguer div:nth-child(1)');
        const thirdHamBar = document.querySelector('#hamburguer div:nth-child(3)');
        let counter;

        const showNav = () => {
          if (!window.matchMedia("(min-width: 90vh)").matches) {
            if(showingNav === false) {
              for(counter=0; counter < navBars.length; ++counter) {
                navBars[counter].style.transform = "translateX(-100%)"
              }

              firstHamBar.style.transform = "rotate(40deg) translateX(2vh) scaleX(0.4)"
              thirdHamBar.style.transform = "rotate(-40deg) translateX(2vh) scaleX(0.4)"

              showingNav = true;
            } else {
            for(counter=0; counter < navBars.length; ++counter) {
              navBars[counter].style.transform = "translateX(0%)"
            }

            firstHamBar.style.transform = "none"
            thirdHamBar.style.transform = "none"

            showingNav = false;
          }
        }
      }

      const hamburguer = document.querySelector('#hamburguer')
        
      if(hamburguer) {
        hamburguer.addEventListener('click', showNav)
      }
        for(counter=0; counter < navBars.length; ++counter) {
          navBars[counter].addEventListener('click', showNav)
        }

        const seeMore = document.querySelector('#see_more');
        const cutDiv = document.querySelector('.cut_div')

        if(seeMore) {
          seeMore.addEventListener('click', () => {
            cutDiv.style.maxHeight = "300vh";
            cutDiv.style.overflow = "unset";
            setTimeout(() => {
              seeMore.style.display = "none";
            }, 500);
          })
        }
      }
    }
  }

  render() {
    return (
      <BrowserRouter>
        <div className="App" id="home" style={{opacity:this.state.opacity}}>
          <Route path="/planilhas/controle-financeiro" component={ControleFinanceiro}/>

          <Header/>
          <Subheader/>
          <Features/>
          <Produtos/>
          <Servicos/>
          <Contact/>

        </div>
      </BrowserRouter>
    );
  }
}


export default App;
