import React, {Component} from 'react';
import './style.css';
import Excellogo from '../../assets/ms.svg';
import FirstFeature from '../../assets/feature1.svg';
import SecondFeature from '../../assets/feature2.svg';
import ThirdFeature from '../../assets/feature3.svg';
import WhatsLogo from '../../assets/whatsapp_logo.svg';

class Features extends Component {
    constructor(props) {
        super(props)
        this.state = {
            maxHeight:'unset',
            hideSeeMore:'none'
        }
    }

    render() {
        return (
            <div className="cut_div_container">
                <section id="features_container" className="cut_div">
                    <article>
                        <img src={FirstFeature} alt="sem custo de desenvolvimento"/>
                        <h2>Planilhas prontas, sem custo de desenvolvimento</h2>
                        <h3>Código VBA aberto e fórmulas expostas ao usuário;<br/><br/>Buscando novas soluções? Realizamos orçamento gratuito!</h3>
                    </article>
        
                    <article>
                        <img src={SecondFeature} alt="compra segura e garantida"/>
                        <h2>Compra segura e garantida</h2>
                        <h3>através do PagSeguro;<br/><br/>Compromisso com a satisfação, devoluções garantidas;<br/><br/>Entrega em aproximadamente 4 horas após a compra</h3>
                    </article>
        
                    <article>
                        <img src={ThirdFeature} alt="videoaulas gratuitas de planilhas"/>
                        <h2>Videoaulas grátis</h2>
                        <h3>Tutoriais disponíveis na página dos produtos e em nosso canal do Youtube</h3>
                    </article>
                <span id="see_more">Ver mais</span>
                </section>
        
                <span id="for_ms">para Microsoft Excel <sup>®</sup></span>
        
                <img src={Excellogo} id="excel_img" alt="Excel Logo"/>
                
                <a href="https://wa.me/5511982678183" target="_blank" id="whatsapp_bt" title="Clique para acessar o WhatsApp" rel="noopener noreferrer">
                    <img src={WhatsLogo}/>
                </a>
            </div>
        
        )
    }
}

export default Features;