import React from 'react';
import './style.css';

const Contact = () => (
    <div id="contato">
        <section className="section_intro" id="contact_section">
            <div>
                <h2>Fale Conosco</h2>

                <p>(11) 9 8267-8183</p>
                <p>planilhacessivel@outlook.com</p>
                <p>CNPJ 01.841.635/0001-84</p>
                <a href="#politicas-de-venda" id="selling_politics">Políticas de venda, troca e devolução</a>
            </div>
        <form className="main_form" action="https://formspree.io/ciappina_andre@hotmail.com" method="POST">

            <label>Nome</label>
            <input type="text" name="name"/>

            <label>E-mail</label>
            <input type="email" name="_replyto"/>

            <label>Mensagem</label>
            <textarea name="message"></textarea>
            
            <input id="enviar" type="submit" value="Enviar"/>
        </form>
        </section>
        
        
        <article id="politicas-de-venda">
            <h5>Valores e Forma de Pagamento</h5>
            <p>Os preços são diferenciados para cada produto e estão informados em suas respectivas páginas; O pagamento pode ser efetuado via cartão de crédito ou boleto, em única parcela, através do PagSeguro; Outra forma de pagamento poderá ser solicitada através de nosso site (Fale Conosco);</p>

            <h5>Entrega dos Produtos</h5>
            <p>A programação para a entrega do produto é de até 4 (quatro) horas após a confirmação da venda, no entanto eles podem ser entregues até o dia útil seguinte à confirmação da venda; A entrega dos produtos é feita através de envio direto pelo Planilhacessível para o e-mail informado pelo cliente à PagSeguro, no momento de sua compra;</p>

            <h5>Devoluções</h5>
            <p>O comprador terá até 7 (sete) dias após a compra para requerer a devolução do valor pago e, nesse sentido, deve nos enviar um e-mail e explicar o motivo da desistência da compra; A devolução do valor ao cliente ocorrerá em até dois dias úteis após a liberação do crédito de seu pagamento ao Planilhacessível;</p>
            
            <h5>Reembolso</h5>
            <p>O comprador terá até 7 (sete) dias após a compra para requerer a devolução do valor pago e, nesse sentido, deve nos enviar um e-mail e explicar o motivo da desistência da compra; A devolução do valor ao cliente ocorrerá em até dois dias úteis após a liberação do crédito de seu pagamento ao Planilhacessível;</p>

            <a href="#home" id="return_up">Voltar ao Início</a>
        </article>
    </div>
)

export default Contact;